* {padding:0; margin:0; box-sizing: border-box; list-style: none; font-family: 'Noto Sans KR', sans-serif;}
h1, h2 {display: none;}
a {text-decoration: none;}

/* header */
.header_top {
    display: flex;
    width:100%;
    background: rgb(8, 75, 98);
    height:25px;
    color:#fff;
    font-size: 13px;
}
.header_top>a {
    margin-left: 200px;
    margin-top: 3px;
    color:#fff;
}
.header_top>span {
    width:100%;
    text-align: right;
    margin-right: 200px;
}
.header_top>span>a {
    line-height: 23px;
    padding:0 5px;
    color:#fff;
}
.header_top>span>a:nth-of-type(2){
    height:10px;
    width:1px;
    margin-top: -5px;
}

/* 로고 */
.menu {
    display: flex;
    position:relative;
    height:60px;
    border-bottom: .5px solid #eee;
    width:100%;
}
.menu img {
    margin-left:  130px;
    object-position: 0px -8px;
    object-fit:cover;
    width:163px;
    height:55px;
}

.menu #menu_ul {
    display: flex;
    position:absolute;
    top:50%;
    transform: translateY(-70%);
    left:50%;
    z-index: 100;
    padding:0;
}
.menu .menu_title {
    height: 41px;
    width:160px;
    font-size: 15px; 
    font-weight: 500;
    line-height: 61px;
}
.menu #menu_ul>li a {
    color:#000;
}
.menu_dropdown {
    opacity: 0;
    transition: ease all .3s;
    padding-top: 10px;
    transform: translateX(-30px);
    margin-top: -10px;
}
.menu #menu_ul:hover .menu_dropdown{
    opacity:1;
}
.dropDownBgClose {
    height: 0px;
    opacity: 0;
}
.dropDownBgOpen {
    height:250px;
    opacity: 1;
    background: rgba( 255, 255, 255, 0.3 );
    backdrop-filter: blur( 9px );
    -webkit-backdrop-filter: blur( 9px );
    border-radius: 10px;
    width:100%;
    z-index: 99;
    position:absolute;
    top:41px;
    left:0;
    transition: ease all .3s;
}
#menu_ul .menu_dropdown li {
    line-height: 40px;
}
#menu_ul .menu_dropdown li a {
    font-size: 14px;
    color:rgb(14, 45, 81);
    font-weight: 700;
}
#menu_ul .menu_dropdown li ul li {
    line-height: 20px;
    transform: translateX(-20px);
    width:130px;
}
#menu_ul .menu_dropdown li ul li:first-child{
    margin-top: -10px;
}
#menu_ul .menu_dropdown li ul li a {
    font-size: 11px;
    font-weight: 700;
}

/* Top버튼 */
.topBtn {
    border: none;
    background: none;
    font-weight: 900;
    font-size: 30px;
    color:#062d51;
    position:fixed;
    bottom: 50px;
    right:50px;
    z-index: 100;
    cursor: pointer;
    transition: ease all 1s;
    border-radius: 20%;
    width:40px;
    height:40px;
    line-height: 30px;
}
.topBtn:hover {
    background: #062d51;
    color:#fff;
    animation: topBtnAnimation ease 2s infinite;
}

@keyframes topBtnAnimation {
    0% {transform: translateY(0px);}
    100% {transform: translateY(-20px);}
}

/* main(slide)*/
.swiper-fixed {
    margin-top: -15px;
}

.swiper-button-next::after,
.swiper-button-prev::after {
    color:rgba(255, 255, 255, .5);
}
.swiper-pagination{
    margin-bottom: 20px;
}
.swiper-pagination .swiper-pagination-bullet {
    opacity: 1;
    background-color: white;
}
.swiper-pagination .swiper-pagination-bullet-active {
    background-color: #12377b;
}

/* what we do, what's news */
.intermediate_guide {
    width:100%;
    text-align: center;
    padding: 30px 0;
}
.intermediate_guide svg {
    display: block;
    margin: auto;
}
.intermediate_guide>p:nth-of-type(1) {
    font-size: 10px;
    color:#010b62;
    font-weight: 700;
}
.intermediate_guide>p:nth-of-type(2) {
    font-size: 11px;
    color:#555;
    font-weight: 500;
    letter-spacing: -1px;
}
.intermediate_guide>span {
    font-weight: 700;
    font-size: 20px;
    letter-spacing: -1px;
}

/* Main 주요사업 안내 */
.business table {
    border-spacing: 0px;
}
.business img {
    width:calc(100vw / 3);
}
.business td {
    background: rgb(4, 46, 65);
    color:#fff;
    width:calc(100vw / 3);
    position:relative;
}
.business td:not([class=no_click]) {
    cursor: pointer;
    transition: ease all .3s;
}
.business td:not([class=no_click]):hover {
    background: rgb(19, 95, 131);
}
.business a {
    color:#fff;
}
.business h3 {
    padding:20px 0 5px 40px;
    letter-spacing: -1px;
    font-weight: 500;
    font-size: 22px;
    line-height: 15px;
}
.business p {
    font-size: 13px;
    padding:0 80px 0 40px;
    max-width:calc(100vw / 3);
}
.business span {
    text-indent: -9999px;
    overflow: hidden;
    position:absolute;
    top:50%;
    transform: translateY(-50%);
    right:40px;
    background: no-repeat url(../public/img_index/arrow_02_next.gif);
    background-size: contain;
    width:15px;
    height:15px;
}

/* Main 공지사항 */
.main_news_notice {
    margin:auto;
    max-width:calc(100% / 3);
}
.main_notice_wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(100px, auto);
    grid-gap: 10px;
    justify-content: center;
}
.main_notice {
    position:relative;
    cursor: pointer;
}
.main_notice a {
    color:#000;
}
.main_notice_wrapper > div:nth-child(1) {
    grid-column: 1/5;
    grid-row: 1;
    display: flex;
}
.main_notice_wrapper > div:nth-child(1) img {
    min-width:320px;
    max-width:500px;
}

.latest_title {
    font-size: 20px;
    font-weight: 700;
    margin: 15px;
}
.main_notice_wrapper > div:nth-child(2) {
    grid-column: 1;
    grid-row: 2;
}
.main_notice_wrapper > div:nth-child(3) {
    grid-column: 2;
    grid-row: 2;
}
.main_notice_wrapper > div:nth-child(4) {
    grid-column: 3;
    grid-row: 2;
}
.main_notice_wrapper > div:nth-child(5) {
    grid-column: 4;
    grid-row: 2;
}
.main_notice img {
    max-width: 150px;
}
.main_notice p:not([class=latest_title]) {
    font-weight: 600;
    font-size: 12px;
    margin: 5px;
}
.main_notice span {
    font-size: 11px;
    position:absolute;
    bottom:5px;
    right:5px;
    display: block;
}

/* footer */

